@import '../variables';

.brands {
  
  @media (max-width: 820px) {
    overflow: scroll;
    overflow-y: hidden;
    max-width: 500px;
  }

  @media (max-width: 540px) {
    max-width: 300px;
  }
  
  ul {
    display: flex;

    li {
      background-color: $grey-bg;
      padding: 13px 30px;
      border-radius: 30px;
      margin-right: 10px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;
      @include noselect();

      &:hover {
        background-color: darken(#f9f9f9, 3%);
      }

      &:active {
        background-color: darken(#f9f9f9, 5%);
      }

      &.active {
        background-color: #282828;
        color: #fff;
      }
    }
  }
}
